// serverapi
import {get, post, reUrl} from '../axiosconfig/axiosconfig'

// vue
export default {
    //----GET-------------------------------------------------------------
    // get 
    testGet: function (params) {
        return get(reUrl + '/test_get/', params)
    },
    //----POST------------------------------------------------------------
    // post 
    testPost: function (params) {
        return post(reUrl + '/test_post/', params, {showLoad: true})
    }
}



