// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
//  bk-magic-vue 
import 'bk-magic-vue/dist/bk-magic-vue.min.css'
import '@/assets/componentLibrary/demand-import';
// bk-magic-vue
// import '@/assets/componentLibrary/fully-import';
// 
import Echarts from 'echarts'
// API
import api from './api/index'
//
import './controller/func/common.js';
// 
import './assets/index'
// 
import Dire from './directive/index.js'
// 
import Component from './components/index.js'
//vuex
import store from './store/index'
import 'jquery'

Vue.use(Echarts);
Vue.use(Dire);
Vue.use(Component);
Vue.prototype.$echarts = Echarts;
// API
Vue.prototype.$http = axios;
Vue.prototype.$api = api;
const headTheme = 'light'; // light  blue
Vue.prototype.headTheme = headTheme;


/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    data() {
        return {
            website: '',
        }
    },
    components: {App},
    template: '<App/>',
});



